<template>
  <div class="user">
    <div class="photo" style="margin-top:5px;">
      <img :src="foto" alt="avatar" />
    </div>
    <div class="user-info">
      <a
        data-toggle="collapse"
        :aria-expanded="!isClosed"
        @click.capture="clicked"
      >
        <span>
          {{ nombre }} <br/> {{usuario}}
        </span>
      </a>

    </div>
  </div>
</template>
<script>
import { CollapseTransition } from "vue2-transitions";

export default {
  components: {
    CollapseTransition
  },
  props: {
    nombre: {
      type: String,
    },
    foto: {
      type: String,
    },
    usuario: {
      type: String,
    }
  },
  data() {
    return {
      isClosed: true
    };
  },
  methods: {
    clicked: function(e) {
      this.$router.push('/usuario')
    },
  }
};
</script>
<style>
.collapsed {
  transition: opacity 1s;
}
</style>
