<template>
  <div class="md-layout">
    <div class="md-layout-item md-size-100">
      <md-card>
        <md-card-header class="md-card-header-icon md-card-header-green">
          <div class="card-icon">
            <md-icon>wallpaper</md-icon>
          </div>
          <h4 class="title">{{tituloView}}</h4>
        </md-card-header>
        <md-card-content>
          <div class="md-layout">
            <div class="md-layout-item md-size-100 text-center mb-10">
              <div class="file-input">
                <div v-if="!image" class="image-container" >
                  <img :src="avatar" title="" />
                </div>
                <div v-else class="image-container">
                  <img :src="image" />
                </div>
                <div class="button-container">
                  <md-button class="md-danger md-round" @click="removeImage()" v-if="image">
                    <i class="fa fa-times"></i>Remover
                  </md-button>
                  <md-button class="md-success md-round md-fileinput">
                    <template v-if="!image">Seleccionar imagen</template>
                    <template v-else>Cambiar</template>
                    <input type="file" name="circle" @change="onFileChange" ref="fotoFile" v-validate="'required|mimes:image/*'" data-vv-as="Imagen" data-vv-name="file"/>
                  </md-button>
                </div>
                <span v-show="errorsback.file" style="color:red">: {{(errorsback.file)?errorsback.file[0]:''}}</span>
                <span v-show="errors.has('file')" style="color:red">{{ errors.first('file')}}</span>
              </div>
            </div>
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100 mb-10">
              <md-field :class="[{ 'md-valid': !errors.has('titulo') && touched.titulo},{ 'md-error': errors.has('titulo')||errorsback.titulo }]">
                <label>Título<i style="color:red">*</i><slide-y-down-transition><span v-show="errorsback.titulo">: {{(errorsback.titulo)?errorsback.titulo[0]:''}}</span></slide-y-down-transition></label>
                <md-input v-model="titulo" type="text" v-validate="'required|max:255'" data-vv-name="titulo" data-vv-as="Título"></md-input>
                <span v-show="errors.has('titulo')" class="md-helper-text">{{ errors.first('titulo')}}</span>
                <slide-y-down-transition> <md-icon class="error" v-show="errors.has('titulo')||errorsback.titulo">close</md-icon> </slide-y-down-transition>
                <slide-y-down-transition> <md-icon class="success" v-show="!errors.has('titulo') && touched.titulo && !errorsback.titulo">done</md-icon> </slide-y-down-transition>
              </md-field>
            </div>
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100 mb-10">
                <md-datepicker v-model="fecha_fin" md-immediately v-validate="'required'" data-vv-name="fecha_fin" data-vv-as="Fecha del evento" :class="[{ 'md-valid': !errors.has('fecha_fin') && touched.fecha_fin},{ 'md-error': errors.has('fecha_fin')||errorsback.fecha_fin }]">
                  <label>Fecha del evento<i style="color:red">*</i><slide-y-down-transition><span v-show="errorsback.fecha_fin">: {{(errorsback.fecha_fin)?errorsback.fecha_fin[0]:''}}</span></slide-y-down-transition></label>
                  <span v-show="errors.has('fecha_fin')" class="md-helper-text">{{ errors.first('fecha_fin')}}</span>
                  <slide-y-down-transition> <md-icon class="error" v-show="errors.has('fecha_fin')||errorsback.fecha_fin">close</md-icon> </slide-y-down-transition>
                  <slide-y-down-transition> <md-icon class="success" v-show="!errors.has('fecha_fin') && touched.fecha_fin && !errorsback.fecha_fin">done</md-icon> </slide-y-down-transition>
                </md-datepicker>
            </div> 
          </div>
        </md-card-content>
        <md-card-actions class="md-alignment-left">
          <md-button class="md-success" @click="validateBeforeSubmit()">Enviar</md-button>
        </md-card-actions>
      </md-card>
    </div>
  </div>
</template>
<script>
import {urlBase,urlImagen} from "../../../urlBase";
import Swal from "sweetalert2";
import moment from 'moment'
import { SlideYDownTransition } from "vue2-transitions";
export default {
  props: {
    avatar: {
      type: String,
      default: require('@/assets/img/silueta-cartel.png')
    },
    id:{
      default:''
    }
  },
  components: {
    SlideYDownTransition
  },
  data() {
    return {
      titulo: "",
      fecha_fin: "",
      image: "",
      url: urlBase,
      urlImg: urlImagen,
      tituloView:(this.id=='')?'Agregar Cartel':'Editar Cartel',
      touched: {
        titulo: false,
        fecha_fin: false
      },
      errorsback:''
    };
  },
  created(){
    var meses = ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'];
    var dias = ['Lunes','Martes','Miercoles','Jueves','Viernes','Sabado','Domingo']
    var mesesabrev = ['Ene', 'Feb', 'Mar', 'Abr', 'May', 'Jun', 'Jul', 'Ago', 'Sep', 'Oct', 'Nov', 'Dic'];
    var diasabrev = ['Lun','Mar','Mie','Jue','Vie','Sab','Dom']
    var mesesIni = ['E', 'F', 'Mar', 'Ab', 'May', 'Jun', 'Jul', 'Ag', 'S', 'O', 'N', 'D'];
    var diasIni = ['L','M','M','J','V','S','D']
    this.$material.locale.months = meses
    this.$material.locale.days = dias
    this.$material.locale.shortMonths = mesesabrev
    this.$material.locale.shortDays = diasabrev
    this.$material.locale.shorterMonths = mesesIni
    this.$material.locale.shorterDays = diasIni
    if(this.id!=''){
      this.getCartel(this.id);
    }
  },
  watch: {
    titulo() { this.touched.titulo = true; },
    fecha_fin() { this.touched.fecha_fin = true; }
  },
  methods: {
    validateBeforeSubmit() {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.addCartel()
        } 
        else {
          Swal.fire({
              title: '¡Aún no es posible guardar!',
              text: 'Ingrese los campos obligatorios',
              type: 'warning',
              confirmButtonText: 'Entendido'
          });
        }
      });
    },
    removeImage: function() {
      this.image = ""
    },
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.createImage(files[0]);
    },
    createImage(file) {
      if(file.type=="image/png"||file.type=="image/jpg"||file.type=="image/jpeg"){
        var reader = new FileReader();
        var vm = this;
        reader.onload = e => {
          vm.image = e.target.result;
        };
        reader.readAsDataURL(file);
      }
    },
    async addCartel(){
      const config = { headers: { "Content-Type": "multipart/form-data" } };
      let formData = new FormData()
      formData.append("titulo", this.titulo);
      formData.append("fecha_fin",moment(this.fecha_fin).format('YYYY/MM/DD'))
      formData.append("file", this.$refs.fotoFile.files[0])
      formData.append("id", this.id)
      try {
        let resp = await axios.post(`${this.url}/cartel`,formData,config)
        if(resp){
          Swal.fire({
            title: '¡Guardado correctamente!',
            text: 'Éste cartel fue guardado exitosamente.',
            type: 'success',
            confirmButtonText: 'Entendido'
          })
          .then((result) => {
              if (result.value) {
                  this.$router.push({ name: 'Carteles'})
              }
          })
        }
      } catch (error) {
        this.errorsback = error.response.data.errors
      }
    },
    async getCartel(id) {
      try {
        var { data } = await axios.get(`${this.url}/cartel/${id}`);
        this.titulo = data.titulo;
        this.fecha_fin = data.fecha_fin;
        this.image = this.urlImg+data.imagen;
      } catch (error) {
        console.log(error);
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.md-card .md-card-actions {
  border: none;
}

.md-inline-checkboxes {
  display: inline-flex;

  .md-checkbox {
    margin-top: 15px !important;
  }
}

.md-checkbox,
.md-radio {
  margin-top: 15px;
  margin-bottom: 0.5rem;
}

.md-checkbox,
.md-radio {
  display: flex;
}

.md-radio /deep/ .md-radio-container {
  margin-left: 5px;
  position: relative;
  left: -3px;
}

.md-form-label + .md-layout-item .md-checkbox:not(:first-child),
.md-form-label + .md-layout-item + .md-layout-item .md-radio:not(:first-child),
.md-form-label + .md-layout-item .md-radio {
  margin-top: 0;
}

.form-control-static {
  margin-top: 6px;
}
</style>
