<template>
  <div class="md-layout">
    <div class="md-layout-item md-size-100">
      <md-card>
        <md-card-header class="md-card-header-icon md-card-header-green">
          <div class="card-icon">
            <md-icon>image</md-icon>
          </div>
          <h4 class="title">{{titulo}}</h4>
        </md-card-header>
        <md-card-content>
          <div class="md-layout">
            <div class="md-layout-item md-size-100 text-center mb-10">
              <div class="file-input">
                <div v-if="!image" class="image-container" >
                  <img :src="avatar" title="" />
                </div>
                <div v-else class="image-container">
                  <img :src="image" />
                </div>
                <div class="button-container">
                  <md-button class="md-danger md-round" @click="removeImage()" v-if="image">
                    <i class="fa fa-times"></i>Remover
                  </md-button>
                  <md-button class="md-success md-round md-fileinput">
                    <template v-if="!image">Seleccionar imagen</template>
                    <template v-else>Cambiar</template>
                    <input type="file" name="circle" @change="onFileChange" ref="fotoFile" v-validate="'required|mimes:image/*'" data-vv-as="Imagen" data-vv-name="file"/>
                  </md-button>
                </div>
                <span v-show="errorsback.file" style="color:red">{{(errorsback.file)?errorsback.file[0]:''}}</span>
                <span v-show="errors.has('file')" style="color:red">{{ errors.first('file')}}</span>
              </div>
            </div>
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100 mb-10">
              <md-field :class="[{ 'md-valid': !errors.has('empresa') && touched.empresa},{ 'md-error': errors.has('empresa')||errorsback.empresa }]">
                <label>Empresa<i style="color:red">*</i><slide-y-down-transition><span v-show="errorsback.empresa">: {{(errorsback.empresa)?errorsback.empresa[0]:''}}</span></slide-y-down-transition></label>
                <md-input v-model="empresa" type="text" v-validate="'required|max:200'" data-vv-name="empresa" data-vv-as="Empresa"></md-input>
                <span v-show="errors.has('empresa')" class="md-helper-text">{{ errors.first('empresa')}}</span>
                <slide-y-down-transition> <md-icon class="error" v-show="errors.has('empresa')||errorsback.empresa">close</md-icon> </slide-y-down-transition>
                <slide-y-down-transition> <md-icon class="success" v-show="!errors.has('empresa') && touched.empresa && !errorsback.empresa">done</md-icon> </slide-y-down-transition>
              </md-field>
            </div>
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100 mb-10">
                <md-datepicker v-model="fecha_inicio" md-immediately v-validate="'required'" data-vv-name="fecha_inicio" data-vv-as="Fecha de inicio" :class="[{ 'md-valid': !errors.has('fecha_inicio') && touched.fecha_inicio},{ 'md-error': errors.has('fecha_inicio')||errorsback.fecha_inicio }]">
                  <label>Fecha de inicio<i style="color:red">*</i><slide-y-down-transition><span v-show="errorsback.fecha_inicio">: {{(errorsback.fecha_inicio)?errorsback.fecha_inicio[0]:''}}</span></slide-y-down-transition></label>
                  <span v-show="errors.has('fecha_inicio')" class="md-helper-text">{{ errors.first('fecha_inicio')}}</span>
                  <slide-y-down-transition> <md-icon class="error" v-show="errors.has('fecha_inicio')||errorsback.fecha_inicio">close</md-icon> </slide-y-down-transition>
                  <slide-y-down-transition> <md-icon class="success" v-show="!errors.has('fecha_inicio') && touched.fecha_inicio && !errorsback.fecha_inicio">done</md-icon> </slide-y-down-transition>
                </md-datepicker>
            </div>    
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100 mb-10">
                <md-datepicker v-model="fecha_fin" md-immediately v-validate="'required'" data-vv-name="fecha_fin" data-vv-as="Fecha de termino" :class="[{ 'md-valid': !errors.has('fecha_fin') && touched.fecha_fin},{ 'md-error': errors.has('fecha_fin')||errorsback.fecha_fin }]">
                  <label>Fecha de termino<i style="color:red">*</i><slide-y-down-transition><span v-show="errorsback.fecha_fin">: {{(errorsback.fecha_fin)?errorsback.fecha_fin[0]:''}}</span></slide-y-down-transition></label>
                  <span v-show="errors.has('fecha_fin')" class="md-helper-text">{{ errors.first('fecha_fin')}}</span>
                  <slide-y-down-transition> <md-icon class="error" v-show="errors.has('fecha_fin')||errorsback.fecha_fin">close</md-icon> </slide-y-down-transition>
                  <slide-y-down-transition> <md-icon class="success" v-show="!errors.has('fecha_fin') && touched.fecha_fin && !errorsback.fecha_fin">done</md-icon> </slide-y-down-transition>
                </md-datepicker>
            </div>
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100 mb-10">
              <md-field :class="[{ 'md-valid': !errors.has('posicion') && touched.posicion},{ 'md-error': errors.has('posicion')||errorsback.posicion }]">
                <label for="posicion">Posición<i style="color:red">*</i><slide-y-down-transition><span v-show="errorsback.posicion">: {{(errorsback.posicion)?errorsback.posicion[0]:''}}</span></slide-y-down-transition></label>
                <md-select v-model="posicion" name="posicion" id="posicion" v-validate="'required'" data-vv-name="posicion" data-vv-as="Posición">
                  <md-option value="1">Home 1</md-option>
                  <md-option value="2">Home 2</md-option>
                  <md-option value="3">Home 3</md-option>
                  <md-option value="4">Home 4</md-option>
                  <md-option value="5">Home 5</md-option>
                  <md-option value="6">Home 6</md-option>
                  <md-option value="7">Home 7</md-option>
                  <md-option value="8">Secciones 1</md-option>
                  <md-option value="9">Secciones 2</md-option>
                  <md-option value="10">Secciones 3</md-option>
                  <md-option value="11">Secciones 4</md-option>
                  <md-option value="12">Secciones 5</md-option>
                  <md-option value="13">Secciones 6</md-option>
                  <md-option value="14">Interiores 1</md-option>
                  <md-option value="15">Interiores 2</md-option>
                  <md-option value="16">Interiores 3</md-option>
                  <md-option value="17">Interiores 4</md-option>
                  <md-option value="18">Interiores 5</md-option>
                  <md-option value="19">Interiores 6</md-option>
                </md-select>
                <span v-show="errors.has('posicion')" class="md-helper-text">{{ errors.first('posicion')}}</span>
                <slide-y-down-transition> <md-icon class="error" v-show="errors.has('posicion')||errorsback.posicion">close</md-icon> </slide-y-down-transition>
                <slide-y-down-transition> <md-icon class="success" v-show="!errors.has('posicion') && touched.posicion && !errorsback.posicion">done</md-icon> </slide-y-down-transition>
              </md-field>
            </div>
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100 mb-10">
              <md-field :class="[{ 'md-valid': !errors.has('estatus') && touched.estatus},{ 'md-error': errors.has('estatus')||errorsback.posicion }]">
                <label for="estatus">Estatus<i style="color:red">*</i><slide-y-down-transition><span v-show="errorsback.estatus">: {{(errorsback.estatus)?errorsback.estatus[0]:''}}</span></slide-y-down-transition></label>
                <md-select v-model="estatus" name="estatus" id="estatus" v-validate="'required'" data-vv-name="estatus" data-vv-as="Estatus">
                  <md-option value="1">Activo</md-option>
                  <md-option value="0">Inactivo</md-option>
                </md-select>
                <span v-show="errors.has('estatus')" class="md-helper-text">{{ errors.first('estatus')}}</span>
                <slide-y-down-transition> <md-icon class="error" v-show="errors.has('estatus')||errorsback.estatus">close</md-icon> </slide-y-down-transition>
                <slide-y-down-transition> <md-icon class="success" v-show="!errors.has('estatus') && touched.estatus && !errorsback.estatus">done</md-icon> </slide-y-down-transition>
              </md-field>
            </div>
          </div>
        </md-card-content>
        <md-card-actions class="md-alignment-left">
          <md-button class="md-success" @click="validateBeforeSubmit()">Enviar</md-button>
        </md-card-actions>
      </md-card>
    </div>
  </div>
</template>
<script>
import {urlBase,urlImagen} from "../../../urlBase";
import Swal from "sweetalert2";
import moment from 'moment'
import { SlideYDownTransition } from "vue2-transitions";
export default {
  props: {
    avatar: {
      type: String,
      default: require('@/assets/img/silueta-banner.png')
    },
    id:{
      default:''
    }
  },
  components: {
    SlideYDownTransition
  },
  data() {
    return {
      empresa: "",
      fecha_inicio: "",
      fecha_fin: "",
      posicion:"",
      estatus:"",
      image: "",
      url: urlBase,
      urlImg: urlImagen,
      titulo:(this.id=='')?'Agregar Banner':'Editar Banner',
      errorsback:[],
      touched: {
        empresa: false,
        fecha_inicio: false,
        fecha_fin: false,
        posicion: false,
        estatus: false
      }
    };
  },
  created(){
    var meses = ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'];
    var dias = ['Lunes','Martes','Miercoles','Jueves','Viernes','Sabado','Domingo']
    var mesesabrev = ['Ene', 'Feb', 'Mar', 'Abr', 'May', 'Jun', 'Jul', 'Ago', 'Sep', 'Oct', 'Nov', 'Dic'];
    var diasabrev = ['Lun','Mar','Mie','Jue','Vie','Sab','Dom']
    var mesesIni = ['E', 'F', 'Mar', 'Ab', 'May', 'Jun', 'Jul', 'Ag', 'S', 'O', 'N', 'D'];
    var diasIni = ['L','M','M','J','V','S','D']
    this.$material.locale.months = meses
    this.$material.locale.days = dias
    this.$material.locale.shortMonths = mesesabrev
    this.$material.locale.shortDays = diasabrev
    this.$material.locale.shorterMonths = mesesIni
    this.$material.locale.shorterDays = diasIni
    if(this.id!=''){
      this.getBanner(this.id);
    }
  },
  watch: {
    empresa() { this.touched.empresa = true; },
    fecha_inicio() { this.touched.fecha_inicio = true; },
    fecha_fin() { this.touched.fecha_fin = true; },
    posicion() { this.touched.posicion = true; },
    estatus() { this.touched.estatus = true; },
  },
  methods: {
    validateBeforeSubmit() {
      this.$validator.validateAll().then((result) => {
        if (result) {
            this.addBanner()
        } 
        else {
          Swal.fire({
              title: '¡Aún no es posible guardar!',
              text: 'Verifique los campos marcados en rojo',
              type: 'warning',
              confirmButtonText: 'Entendido'
          });
        }
      });
    },
    removeImage: function() {
      this.image = ""
    },
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.createImage(files[0]);
    },
    createImage(file) {
      if(file.type=="image/png"||file.type=="image/jpg"||file.type=="image/jpeg"){
        var reader = new FileReader();
        var vm = this;
        reader.onload = e => {
          vm.image = e.target.result;
        };
        reader.readAsDataURL(file);
      }
    },
    async addBanner(){
      const config = { headers: { "Content-Type": "multipart/form-data" } };
      let formData = new FormData()
      formData.append("empresa", this.empresa);
      formData.append("fecha_inicio", moment(this.fecha_inicio).format('YYYY/MM/DD'))
      formData.append("fecha_fin",moment(this.fecha_fin).format('YYYY/MM/DD'))
      formData.append("file", this.$refs.fotoFile.files[0])
      formData.append("estatus", this.estatus)
      formData.append("posicion", this.posicion)
      formData.append("id", this.id)
      try {
        let resp = await axios.post(`${this.url}/banner`,formData,config)
        if(resp){
          Swal.fire({
            title: '¡Guardado correctamente!',
            text: 'Éste banner fue guardado exitosamente.',
            type: 'success',
            confirmButtonText: 'Entendido'
          })
          .then((result) => {
              if (result.value) {
                  this.$router.push({ name: 'Banners'})
              }
          })
        }
      } catch (error) {
        this.errorsback = error.response.data.errors
      }
    },
    async getBanner(id) {
      try {
        var { data } = await axios.get(`${this.url}/banner/${id}`);
        this.empresa = data.empresa;
        this.fecha_inicio = data.fecha_inicio;
        this.fecha_fin = data.fecha_fin;
        this.image = this.urlImg+data.imagen;
        this.estatus = data.estatus;
        this.posicion = data.posicion;
      } catch (error) {
        console.log(error);
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.md-card .md-card-actions {
  border: none;
}

.md-inline-checkboxes {
  display: inline-flex;

  .md-checkbox {
    margin-top: 15px !important;
  }
}

.md-checkbox,
.md-radio {
  margin-top: 15px;
  margin-bottom: 0.5rem;
}

.md-checkbox,
.md-radio {
  display: flex;
}

.md-radio /deep/ .md-radio-container {
  margin-left: 5px;
  position: relative;
  left: -3px;
}

.md-form-label + .md-layout-item .md-checkbox:not(:first-child),
.md-form-label + .md-layout-item + .md-layout-item .md-radio:not(:first-child),
.md-form-label + .md-layout-item .md-radio {
  margin-top: 0;
}

.form-control-static {
  margin-top: 6px;
}
</style>
