import DashboardLayout from "@/pages/Dashboard/Layout/DashboardLayout.vue";
import Home from "@/pages/Admin/Home.vue";
import Autores from "@/pages/Admin/Autores/Autores.vue";
import FormAutores from "@/pages/Admin/Autores/FormAutores.vue";
import Banners from "@/pages/Admin/Banners/Banners.vue";
import FormBanners from "@/pages/Admin/Banners/FormBanners.vue";
import Articulos from "@/pages/Admin/Articulos/Articulos.vue";
import FormArticulos from "@/pages/Admin/Articulos/FormArticulos.vue";
import Carteles from "@/pages/Admin/Carteles/Carteles.vue";
import FormCarteles from "@/pages/Admin/Carteles/FormCarteles.vue";
import Noticias from "@/pages/Admin/Noticias/Noticias.vue";
import FormNoticias from "@/pages/Admin/Noticias/FormNoticias.vue";
import CropNoticias from "@/pages/Admin/Noticias/CropNoticias.vue";
import AuthLayout from "@/pages/Admin/AuthLayout.vue";
import Login from "@/pages/Admin/Login.vue";
import Usuario from "@/pages/Admin/Usuario.vue";
import Cerrar from "@/pages/Admin/Logout.vue";
const routes = [
  {
    path: "/",
    redirect: "/login"
  },
  {
    path: "/enbreveback/public/admin/",
    redirect: "/login",
  },
  {
    path: "/",
    component: AuthLayout,
    name: "Authentication",
    children: [
      {
        path: "/login",
        name: "Login",
        component: Login
      }
    ]
  },
  {
    path: "/",
    component: DashboardLayout,
    children: [
      {
        path: "home",
        name: "Inicio",
        components: { default: Home }
      },
      {
        path: "social",
        name: "Social",
        components: { default: Noticias },
      },
      {
        path: "cultural",
        name: "Cultural",
        components: { default: Noticias },
      },
      { 
        path: '/noticia/:id', 
        name: "EditSocial",
        component: FormNoticias, 
        props: true 
      },
      { 
        path: '/noticia', 
        name: "AddSocial",
        component: FormNoticias, 
        props: true 
      },
      { 
        path: '/noticia/:id', 
        name: "EditCultural",
        component: FormNoticias, 
        props: true 
      },
      { 
        path: '/noticia', 
        name: "AddCultural",
        component: FormNoticias, 
        props: true 
      },
      {
        path: "autores",
        name: "Autores",
        components: { default: Autores }
      },
      { 
        path: '/autor/:id', 
        name: "EditAutor",
        component: FormAutores, 
        props: true 
      },
      { 
        path: '/autor', 
        name: "AddAutor",
        component: FormAutores, 
        props: true 
      },
      {
        path: "banners",
        name: "Banners",
        components: { default: Banners }
      },
      { 
        path: '/banner/:id', 
        name: "EditBanner",
        component: FormBanners, 
        props: true 
      },
      { 
        path: '/banner', 
        name: "AddBanner",
        component: FormBanners, 
        props: true 
      },
      {
        path: "articulos",
        name: "Articulos",
        components: { default: Articulos }
      },
      { 
        path: '/articulo/:id', 
        name: "EditArticulo",
        component: FormArticulos, 
        props: true 
      },
      { 
        path: '/articulo', 
        name: "AddArticulo",
        component: FormArticulos, 
        props: true 
      },
      {
        path: "carteles",
        name: "Carteles",
        components: { default: Carteles }
      },
      { 
        path: '/cartel/:id', 
        name: "EditCartel",
        component: FormCarteles, 
        props: true 
      },
      { 
        path: '/cartel', 
        name: "AddCartel",
        component: FormCarteles, 
        props: true 
      },
      { 
        path: '/cropNoticias', 
        name: "CropNoticias",
        component: CropNoticias, 
        props: true 
      },
      { 
        path: '/usuario', 
        name: "Usuario",
        component: Usuario
      },
      { 
        path: '/cerrar', 
        name: "Cerrar sesión",
        component: Cerrar
      }
    ]
  }
];

export default routes;