<template>
  <div class="md-layout">
    <div class="md-layout-item md-size-100">
      <md-card>
        <md-card-header class="md-card-header-icon md-card-header-green">
          <div class="card-icon">
            <md-icon>portrait</md-icon>
          </div>
          <h4 class="title">{{titulo}}</h4>
        </md-card-header>
        <md-card-content>
          <div class="md-layout">
            <div class="md-layout-item md-size-100 text-center mb-10">
              <div class="file-input">
                <div v-if="!image" class="image-container" >
                  <img :src="avatar" title="" />
                </div>
                <div v-else class="image-container">
                  <img :src="image" />
                </div>
                <div class="button-container">
                  <md-button class="md-danger md-round" @click="removeImage()" v-if="image">
                    <i class="fa fa-times"></i>Remover
                  </md-button>
                  <md-button class="md-success md-round md-fileinput">
                    <template v-if="!image">Seleccionar imagen</template>
                    <template v-else>Cambiar</template>
                    <input type="file" name="circle" @change="onFileChange" ref="fotoFile"/>
                  </md-button>
                </div>
              </div>
            </div>
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100 mb-10">
              <md-field :class="[{ 'md-valid': !errors.has('nombre') && touched.nombre},{ 'md-error': errors.has('nombre')||errorsback.nombre }]">
                <label>Nombre<i style="color:red">*</i><slide-y-down-transition><span v-show="errorsback.nombre">: {{(errorsback.nombre)?errorsback.nombre[0]:''}}</span></slide-y-down-transition></label>
                <md-input v-model="nombre" type="text" v-validate="'required|alpha|max:30'" data-vv-name="nombre" data-vv-as="Nombre"></md-input>
                <span v-show="errors.has('nombre')" class="md-helper-text">{{ errors.first('nombre')}}</span>
                <slide-y-down-transition> <md-icon class="error" v-show="errors.has('nombre')||errorsback.nombre">close</md-icon> </slide-y-down-transition>
                <slide-y-down-transition> <md-icon class="success" v-show="!errors.has('nombre') && touched.nombre && !errorsback.nombre">done</md-icon> </slide-y-down-transition>
              </md-field>
            </div>    
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100 mb-10">
              <md-field :class="[{ 'md-valid': !errors.has('primer_ap')  && touched.primer_ap},{ 'md-error': errors.has('primer_ap')||errorsback.primer_ap }]">
                <label>Apellido paterno<i style="color:red">*</i><slide-y-down-transition><span v-show="errorsback.primer_ap">: {{(errorsback.primer_ap)?errorsback.primer_ap[0]:''}}</span></slide-y-down-transition></label>
                <md-input v-model="primer_ap" type="text" v-validate="'required|alpha|max:20'" data-vv-name="primer_ap" data-vv-as="Apellido paterno"></md-input>
                <span v-show="errors.has('primer_ap')" class="md-helper-text">{{ errors.first('primer_ap')}}</span>
                <slide-y-down-transition> <md-icon class="error" v-show="errors.has('primer_ap')||errorsback.primer_ap">close</md-icon> </slide-y-down-transition>
                <slide-y-down-transition> <md-icon class="success" v-show="!errors.has('primer_ap') && touched.primer_ap && !errorsback.primer_ap">done</md-icon> </slide-y-down-transition>
              </md-field>
            </div>
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100 mb-10">
              <md-field :class="[{ 'md-valid': !errors.has('segundo_ap')  && touched.segundo_ap},{ 'md-error': errors.has('segundo_ap')||errorsback.segundo_ap }]">
                <label>Apellido materno<slide-y-down-transition><span v-show="errorsback.segundo_ap">: {{(errorsback.segundo_ap)?errorsback.segundo_ap[0]:''}}</span></slide-y-down-transition></label>
                <md-input v-model="segundo_ap" type="text" v-validate="'alpha|max:20'" data-vv-name="segundo_ap" data-vv-as="Apellido materno"></md-input>
                <span v-show="errors.has('segundo_ap')" class="md-helper-text">{{ errors.first('segundo_ap')}}</span>
                <slide-y-down-transition> <md-icon class="error" v-show="errors.has('segundo_ap')||errorsback.segundo_ap">close</md-icon> </slide-y-down-transition>
                <slide-y-down-transition> <md-icon class="success" v-show="!errors.has('segundo_ap') && touched.segundo_ap && !errorsback.segundo_ap">done</md-icon> </slide-y-down-transition>
              </md-field>
            </div>
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100 mb-10">
              <md-field :class="[{ 'md-valid': !errors.has('facebook') && touched.facebook},{ 'md-error': errors.has('facebook')||errorsback.facebook }]">
                <label>Facebook<slide-y-down-transition><span v-show="errorsback.facebook">: {{(errorsback.facebook)?errorsback.facebook[0]:''}}</span></slide-y-down-transition></label>
                <md-input v-model="facebook" type="text" v-validate="'max:100'" data-vv-name="facebook" data-vv-as="Facebook"></md-input>
                <span v-show="!errors.has('facebook')" class="md-helper-text">https://www.facebook.com/USUARIO</span>
                <span v-show="errors.has('facebook')" class="md-helper-text">{{ errors.first('facebook')}}</span>
                <slide-y-down-transition> <md-icon class="error" v-show="errors.has('facebook')||errorsback.facebook">close</md-icon> </slide-y-down-transition>
                <slide-y-down-transition> <md-icon class="success" v-show="!errors.has('facebook') && touched.facebook && !errorsback.facebook">done</md-icon> </slide-y-down-transition>
              </md-field>
            </div>    
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100 mb-10">
              <md-field :class="[{ 'md-valid': !errors.has('twitter') && touched.twitter},{ 'md-error': errors.has('twitter')||errorsback.twitter }]">
                <label>Twitter<slide-y-down-transition><span v-show="errorsback.twitter">: {{(errorsback.twitter)?errorsback.twitter[0]:''}}</span></slide-y-down-transition></label>
                <md-input v-model="twitter" type="text" v-validate="'max:100'" data-vv-name="twitter" data-vv-as="Twitter"></md-input>
                <span v-show="!errors.has('twitter')" class="md-helper-text">https://twitter.com/USUARIO</span>
                <span v-show="errors.has('twitter')" class="md-helper-text">{{ errors.first('twitter')}}</span>
                <slide-y-down-transition> <md-icon class="error" v-show="errors.has('twitter')||errorsback.twitter">close</md-icon> </slide-y-down-transition>
                <slide-y-down-transition> <md-icon class="success" v-show="!errors.has('twitter') && touched.twitter && !errorsback.twitter">done</md-icon> </slide-y-down-transition>
              </md-field>
            </div>
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100 mb-10">
              <md-field :class="[{ 'md-valid': !errors.has('instagram') && touched.instagram},{ 'md-error': errors.has('instagram')||errorsback.instagram }]">
                <label>Instagram<slide-y-down-transition><span v-show="errorsback.instagram">: {{(errorsback.instagram)?errorsback.instagram[0]:''}}</span></slide-y-down-transition></label>
                <md-input v-model="instagram" type="text" v-validate="'max:100'" data-vv-name="instagram" data-vv-as="Instagram"></md-input>
                <span v-show="!errors.has('instagram')" class="md-helper-text">https://www.instagram.com/USUARIO</span>
                <span v-show="errors.has('instagram')" class="md-helper-text">{{ errors.first('instagram')}}</span>
                <slide-y-down-transition> <md-icon class="error" v-show="errors.has('instagram')||errorsback.instagram">close</md-icon> </slide-y-down-transition>
                <slide-y-down-transition> <md-icon class="success" v-show="!errors.has('instagram') && touched.instagram && !errorsback.instagram">done</md-icon> </slide-y-down-transition>
              </md-field>
            </div>
            <div class="md-layout-item md-size-100">
              <md-field :class="[{ 'md-valid': !errors.has('descripcion') && touched.descripcion},{ 'md-error': errors.has('descripcion') }]"> 
                <label>Descripción<i style="color:red">*</i></label>
                <md-input v-model="descripcion" type="text" v-validate="'required'" data-vv-name="descripcion" data-vv-as="Descripción"></md-input>
                <span v-show="errors.has('descripcion')" class="md-helper-text">{{ errors.first('descripcion')}}</span>
                <slide-y-down-transition> <md-icon class="error" v-show="errors.has('descripcion')||errorsback.segundo_ap">close</md-icon> </slide-y-down-transition>
                <slide-y-down-transition> <md-icon class="success" v-show="!errors.has('descripcion') && touched.descripcion">done</md-icon> </slide-y-down-transition>
              </md-field>
            </div>
          </div>
        </md-card-content>
        <md-card-actions class="md-alignment-left">
          <md-button class="md-success" @click="validateBeforeSubmit()">Enviar</md-button>
        </md-card-actions>
      </md-card>
    </div>
  </div>
</template>
<script>
import {urlBase,urlImagen} from "../../../urlBase";
import Swal from "sweetalert2";
import { SlideYDownTransition } from "vue2-transitions";
export default {
  props: {
    avatar: {
      type: String,
      default: require('@/assets/img/silueta-autor.png')
    },
    id:{
      default:''
    }
  },
  components: {
    SlideYDownTransition
  },
  data() {
    return {
      nombre: "",
      primer_ap: "",
      segundo_ap: "",
      facebook:"",
      twitter:"",
      instagram:"",
      image: "",
      url: urlBase,
      urlImg: urlImagen,
      descripcion: "",
      titulo:(this.id=='')?'Agregar Autor':'Editar Autor',
      errorsback:[],
      touched: {
        nombre: false,
        primer_ap: false,
        segundo_ap: false,
        facebook: false,
        twitter: false,
        instagram: false,
        descripcion: false
      }
    };
  },
  created(){
    if(this.id!=''){
      this.getAutor(this.id);
    }
  },
  watch: {
    nombre() { this.touched.nombre = true; },
    primer_ap() { this.touched.primer_ap = true; },
    segundo_ap() { this.touched.segundo_ap = true; },
    facebook() { this.touched.facebook = true; },
    twitter() { this.touched.twitter = true; },
    instagram() { this.touched.instagram = true; },
    descripcion() { this.touched.descripcion = true; }
  },
  methods: {
    validateBeforeSubmit() {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.addAutor()
        } 
        else {
          Swal.fire({
              title: '¡Aún no es posible guardar!',
              text: 'Verifique los campos marcados en rojo',
              type: 'warning',
              confirmButtonText: 'Entendido'
          });
        }
      });
    },
    removeImage: function() {
      this.image = ""
    },
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.createImage(files[0]);
    },
    createImage(file) {
      if(file.type=="image/png"||file.type=="image/jpg"||file.type=="image/jpeg"){
        var reader = new FileReader();
        var vm = this;
        reader.onload = e => {
          vm.image = e.target.result;
        };
        reader.readAsDataURL(file);
      }
    },
    async addAutor(){
      const config = { headers: { "Content-Type": "multipart/form-data" } };
      let formData = new FormData()
      formData.append("nombre", this.nombre);
      formData.append("primer_ap", this.primer_ap)
      formData.append("segundo_ap",this.segundo_ap)
      formData.append("file", this.$refs.fotoFile.files[0])
      formData.append("descripcion", this.descripcion)
      formData.append("facebook", this.facebook)
      formData.append("twitter", this.twitter)
      formData.append("instagram", this.instagram)
      formData.append("id", this.id)
      try {
        let resp = await axios.post(`${this.url}/autor`,formData,config)
        if(resp){
          Swal.fire({
            title: '¡Guardado correctamente!',
            text: 'Éste autor fue guardado exitosamente.',
            type: 'success',
            confirmButtonText: 'Entendido'
          })
          .then((result) => {
              if (result.value) {
                  this.$router.push({ name: 'Autores'})
              }
          })
        }
      } catch (error) {
        this.errorsback = error.response.data.errors
      }
    },
    async getAutor(id) {
      try {
        var { data } = await axios.get(`${this.url}/autor/${id}`);
        this.nombre = data.nombre;
        this.primer_ap = data.primer_ap;
        this.segundo_ap = data.segundo_ap;
        this.image = this.urlImg+data.foto;
        this.descripcion = data.descripcion;
        this.facebook = data.facebook;
        this.twitter = data.twitter;
        this.instagram = data.instagram;
      } catch (error) {
        console.log(error);
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.md-card .md-card-actions {
  border: none;
}

.md-inline-checkboxes {
  display: inline-flex;

  .md-checkbox {
    margin-top: 15px !important;
  }
}

.md-checkbox,
.md-radio {
  margin-top: 15px;
  margin-bottom: 0.5rem;
}

.md-checkbox,
.md-radio {
  display: flex;
}

.md-radio /deep/ .md-radio-container {
  margin-left: 5px;
  position: relative;
  left: -3px;
}

.md-form-label + .md-layout-item .md-checkbox:not(:first-child),
.md-form-label + .md-layout-item + .md-layout-item .md-radio:not(:first-child),
.md-form-label + .md-layout-item .md-radio {
  margin-top: 0;
}

.form-control-static {
  margin-top: 6px;
}

.descripcion{
  height: 100px !important;
}
</style>
