<template>
  <div class="md-layout">
    <div class="md-layout-item md-size-100">
      <md-card>
        <md-card-header class="md-card-header-icon md-card-header-green">
          <div class="card-icon">
            <md-icon>portrait</md-icon>
          </div>
          <h4 class="title">Usuario</h4>
        </md-card-header>
        <md-card-content>
          <div class="md-layout">
            <div class="md-layout-item md-size-100 text-center mb-10">
              <div class="file-input">
                <div v-if="!image" class="image-container" >
                  <img :src="avatar" title="" />
                </div>
                <div v-else class="image-container">
                  <img :src="image" />
                </div>
                <div class="button-container">
                  <md-button class="md-danger md-round" @click="removeImage()" v-if="image">
                    <i class="fa fa-times"></i>Remover
                  </md-button>
                  <md-button class="md-success md-round md-fileinput">
                    <template v-if="!image">Seleccionar imagen</template>
                    <template v-else>Cambiar</template>
                    <input type="file" name="circle" @change="onFileChange" ref="fotoFile"/>
                  </md-button>
                </div>
              </div>
            </div>
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100 mb-10">
              <md-field :class="[{ 'md-valid': !errors.has('nombre') && touched.nombre},{ 'md-error': errors.has('nombre') }]">
                <label>Nombre completo<i style="color:red">*</i></label>
                <md-input v-model="nombre" type="text" v-validate="'required'" data-vv-name="nombre" data-vv-as="Nombre completo"></md-input>
                <span v-show="errors.has('nombre')" class="md-helper-text">{{ errors.first('nombre')}}</span>
                <slide-y-down-transition> <md-icon class="error" v-show="errors.has('nombre')">close</md-icon> </slide-y-down-transition>
                <slide-y-down-transition> <md-icon  class="success" v-show="!errors.has('nombre') && touched.nombre">done</md-icon> </slide-y-down-transition>
              </md-field>
            </div>    
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100 mb-10">
              <md-field :class="[{ 'md-valid': !errors.has('usuario')  && touched.usuario},{ 'md-error': errors.has('usuario') }]">
                <label>Usuario<i style="color:red">*</i></label>
                <md-input v-model="usuario" type="text" v-validate="'required'" data-vv-name="usuario" data-vv-as="Usuario"></md-input>
                <span v-show="errors.has('usuario')" class="md-helper-text">{{ errors.first('usuario')}}</span>
                <slide-y-down-transition> <md-icon class="error" v-show="errors.has('usuario')">close</md-icon> </slide-y-down-transition>
                <slide-y-down-transition> <md-icon  class="success" v-show="!errors.has('usuario') && touched.usuario">done</md-icon> </slide-y-down-transition>
              </md-field>
            </div>
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100 mb-10">
              <md-field :class="[{ 'md-valid': !errors.has('contraseña')  && touched.contraseña},{ 'md-error': errors.has('contraseña') }]">
                <label>Contraseña<i style="color:red">*</i></label>
                <md-input v-model="contraseña" type="password" v-validate="'required'" data-vv-name="contraseña" data-vv-as="Contraseña"></md-input>
                <span v-show="errors.has('contraseña')" class="md-helper-text">{{ errors.first('contraseña')}}</span>
                <slide-y-down-transition> <md-icon class="error" v-show="errors.has('contraseña')">close</md-icon> </slide-y-down-transition>
                <slide-y-down-transition> <md-icon  class="success" v-show="!errors.has('contraseña') && touched.contraseña">done</md-icon> </slide-y-down-transition>
              </md-field>
            </div>
          </div>
        </md-card-content>
        <md-card-actions class="md-alignment-left">
          <md-button class="md-success" @click="validateBeforeSubmit()">Enviar</md-button>
        </md-card-actions>
      </md-card>
    </div>
  </div>
</template>
<script>
import {urlBase,urlImagen} from "../../urlBase";
import Swal from "sweetalert2";
import { SlideYDownTransition } from "vue2-transitions";
export default {
  props: {
    avatar: {
      type: String,
      default: require('@/assets/img/silueta-usuario.png')
    },
    id:{
      default:''
    }
  },
  components: {
    SlideYDownTransition
  },
  data() {
    return {
      nombre: "",
      usuario: "",
      contraseña: "",
      image: "",
      url: urlBase,
      urlImg: urlImagen,
      touched: {
        nombre: false,
        usuario: false,
        contraseña: false,
      },
    };
  },
  created(){
    this.getUsuario();
  },
  watch: {
    nombre() { this.touched.nombre = true; },
    usuario() { this.touched.usuario = true; },
    contraseña() { this.touched.contraseña = true; },
  },
  methods: {
    validateBeforeSubmit() {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.addUsuario()
        } 
        else {
          Swal.fire({
              title: '¡Aún no es posible guardar!',
              text: 'Ingrese los campos obligatorios',
              type: 'warning',
              confirmButtonText: 'Entendido'
          });
        }
      });
    },
    removeImage: function() {
      this.image = ""
    },
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.createImage(files[0]);
    },
    createImage(file) {
      var reader = new FileReader();
      var vm = this;
      reader.onload = e => {
        vm.image = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    async addUsuario(){
      const config = { headers: { "Content-Type": "multipart/form-data" } };
      let formData = new FormData()
      formData.append("nombre", this.nombre);
      formData.append("usuario", this.usuario)
      formData.append("contraseña",this.contraseña)
      formData.append("file", this.$refs.fotoFile.files[0])
      try {
        let resp = await axios.post(`${this.url}/usuario`,formData,config)
        if(resp){
          Swal.fire({
            title: '¡Guardado correctamente!',
            text: 'Éste usuario fue guardado exitosamente.',
            type: 'success',
            confirmButtonText: 'Entendido'
          })
          .then((result) => {
              if (result.value) {
                  this.$router.push({ name: 'Inicio'})
              }
          })
        }
      } catch (error) {
        console.log("error "+error);
      }
    },
    async getUsuario() {
      try {
        var { data } = await axios.get(`${this.url}/usuario`);
        this.nombre = data.nombre;
        this.usuario = data.usuario;
        this.contraseña = data.password;
        this.image = this.urlImg+data.foto;
      } catch (error) {
        console.log(error);
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.md-card .md-card-actions {
  border: none;
}

.md-inline-checkboxes {
  display: inline-flex;

  .md-checkbox {
    margin-top: 15px !important;
  }
}

.md-checkbox,
.md-radio {
  margin-top: 15px;
  margin-bottom: 0.5rem;
}

.md-checkbox,
.md-radio {
  display: flex;
}

.md-radio /deep/ .md-radio-container {
  margin-left: 5px;
  position: relative;
  left: -3px;
}

.md-form-label + .md-layout-item .md-checkbox:not(:first-child),
.md-form-label + .md-layout-item + .md-layout-item .md-radio:not(:first-child),
.md-form-label + .md-layout-item .md-radio {
  margin-top: 0;
}

.form-control-static {
  margin-top: 6px;
}

</style>
