<template>
  <div class="md-layout">
    <div class="md-layout-item">
      <md-card>
        <md-card-header class="md-card-header-icon md-card-header-green">
          <div class="md-layout">
            <div class="card-icon">
              <md-icon>image</md-icon>
            </div>
            <div class="md-layout-item">
              <h4 class="title">Banners</h4>
            </div>
            <div class="md-layout-item btn-add">
              <md-button class="md-success md-just-icon md-round" @click.native="handleAdd()">
                <md-icon >image</md-icon>
              </md-button>
            </div>
          </div>
        </md-card-header>
        <md-card-content>
          <md-table :value="queriedData" :md-sort.sync="currentSort" :md-sort-order.sync="currentSortOrder" :md-sort-fn="customSort" class="paginated-table table-striped table-hover">
            <md-table-toolbar>
              <md-field>
                <label for="pages">Por página</label>
                <md-select v-model="pagination.perPage" name="pages">
                  <md-option v-for="item in pagination.perPageOptions" :key="item" :label="item" :value="item">
                    {{ item }}
                  </md-option>
                </md-select>
              </md-field>
              <md-field>
                <md-input type="search" class="mb-3" clearable style="width: 200px" placeholder="Buscar banner" v-model="searchQuery"></md-input>
              </md-field>
            </md-table-toolbar>
            <md-table-row slot="md-table-row" slot-scope="{ item }">
              <md-table-cell md-label="Imagen">
                <img :src="`${urlImg}${item.imagen}`" alt="" class="fotoTabla">
              </md-table-cell>
              <md-table-cell md-label="Empresa">
                {{item.empresa}}
              </md-table-cell>
              <md-table-cell md-label="Inicio">
                {{formatDate(item.fecha_inicio)}}
              </md-table-cell>
              <md-table-cell md-label="Fin">
                {{formatDate(item.fecha_fin)}}
              </md-table-cell>
              <md-table-cell md-label="Posición">
                {{item.posicion}}
              </md-table-cell>
              <md-table-cell md-label="Estatus">
                {{item.estatus}}
              </md-table-cell>
              <md-table-cell md-label="Acciones">
                <md-button class="md-just-icon md-success actionButton" @click.native="handleEdit(item.id)">
                  <md-icon>edit</md-icon>
                </md-button>
                <md-button class="md-just-icon md-danger actionButton" @click.native="handleDelete(item)">
                  <md-icon>delete</md-icon>
                </md-button>
              </md-table-cell>
            </md-table-row>
          </md-table>
          <div class="footer-table md-table">
            <table>
              <tfoot>
                <tr>
                  <th v-for="item in footerTable" :key="item.name" class="md-table-head">
                    <div class="md-table-head-container md-ripple md-disabled">
                      <div class="md-table-head-label">
                        {{ item }}
                      </div>
                    </div>
                  </th>
                </tr>
              </tfoot>
            </table>
          </div>
        </md-card-content>
        <md-card-actions md-alignment="space-between">
          <div class="">
            <p class="card-category">
              Mostrando {{ from + 1 }} a {{ to }} de {{ total }} registros
            </p>
          </div>
          <pagination class="pagination-no-border pagination-success" v-model="pagination.currentPage" :per-page="pagination.perPage" :total="total">
          </pagination>
        </md-card-actions>
      </md-card>
    </div>
  </div>
</template>

<script>
import { Pagination } from "@/components";
import Fuse from "fuse.js";
import Swal from "sweetalert2";
import {urlBase,urlImagen} from "../../../urlBase";
import moment from 'moment'

export default {
  components: {
    Pagination
  },
  computed: {
    queriedData() {
      let result = this.tableData;
      if (this.searchedData.length > 0) {
        result = this.searchedData;
      }
      return result.slice(this.from, this.to);
    },
    to() {
      let highBound = this.from + this.pagination.perPage;
      if (this.total < highBound) {
        highBound = this.total;
      }
      return highBound;
    },
    from() {
      return this.pagination.perPage * (this.pagination.currentPage - 1);
    },
    total() {
      return this.searchedData.length > 0
        ? this.searchedData.length
        : this.tableData.length;
    }
  },
  data() {
    return {
      currentSort: "posicion",
      currentSortOrder: "asc",
      pagination: {
        perPage: 10,
        currentPage: 1,
        perPageOptions: [10, 25, 50],
        total: 0
      },
      footerTable: ["Imagen","Empresa","Inicio", "Fin", "Posición", "Estatus","Acciones"],
      searchQuery: "",
      propsToSearch: [""],
      tableData: [],
      searchedData: [],
      fuseSearch: null,
      url: urlBase,
      urlImg: urlImagen
    };
  },
  methods: {
    customSort(value) {
      return value.sort((a, b) => {
        const sortBy = this.currentSort;
        if (this.currentSortOrder === "desc") {
          return a[sortBy].toString().localeCompare(b[sortBy]);
        }
        return b[sortBy].toString().localeCompare(a[sortBy]);
      });
    },
    handleEdit(id) {
      this.$router.push({ name: "EditBanner", params: { id } });
    },
    handleAdd() {
      this.$router.push({ name: "AddBanner" });
    },
    handleDelete(item) {
      Swal.fire({
        title: "¿Está seguro?",
        text: `Una vez eliminado este banner no se podrá recuperar`,
        type: "warning",
        showCancelButton: true,
        confirmButtonClass: "md-button md-success btn-fill",
        cancelButtonClass: "md-button md-danger btn-fill",
        confirmButtonText: "Si, Eliminar",
        cancelButtonText: "Cancelar",
        buttonsStyling: false
      }).then(async result => {
        if (result.value) {
          let status = await this.deleteRow(item.id);
          if (status) {
            Swal.fire({
              title: "¡Eliminado!",
              text: `Se ha eliminado este banner`,
              type: "success",
              confirmButtonClass: "md-button md-success btn-fill",
              buttonsStyling: false
            });
          } else {
            Swal.fire({
              type: "error",
              title: "Oops...",
              text: "Se presento un problema al eliminar su registro"
            });
          }
        }
      });
    },
    async deleteRow(item) {
      let eliminado;
      try {
        await axios.delete(`${this.url}/banner/${item}`);
        let indexToDelete = this.tableData.findIndex(
          tableRow => tableRow.id === item
        );
        if (indexToDelete >= 0) {
          this.tableData.splice(indexToDelete, 1);
        }
        eliminado = true;
      } catch (error) {
        eliminado = false;
      }
      return eliminado;
    },
    async getBanners() {
      const { data } = await axios.get(`${this.url}/banner`);
      this.tableData = data;
      this.fuseSearch = new Fuse(this.tableData, {
        keys: ["empresa"],
        threshold: 0.3
      });
    },
    formatDate(fecha){
      return moment(fecha).locale('es').format('LL')
    }
  },
  created() {
    this.getBanners();
  },
  watch: {
    searchQuery(value) {
      let result = this.tableData;
      if (value !== "") {
        result = this.fuseSearch.search(this.searchQuery);
      }
      this.searchedData = result;
    }
  }
};
</script>
<style lang="css" scoped>
.md-card .md-card-actions{
  border: 0;
  margin-left: 20px;
  margin-right: 20px;
}
.fotoTabla{
  width: 100px !important;
}
</style>
