<template>
  <div
    class="wrapper"
    :class="[
      { 'nav-open': $sidebar.showSidebar },
      { rtl: $route.meta.rtlActive }
    ]"
  >
    <notifications></notifications>
    <side-bar
      :active-color="sidebarBackground"
      :background-image="sidebarBackgroundImage"
      :data-background-color="sidebarBackgroundColor"
      title="enbreve"
      :logo="require('@/assets/img/eb.png')"
    >
      <user-menu v-if="usuario.foto" :nombre="usuario.nombre" :foto="`${urlImg}${usuario.foto}`" :usuario="usuario.usuario"></user-menu>
      <mobile-menu></mobile-menu>
      <template slot="links">
        <sidebar-item
          :link="{ name: 'Inicio', icon: 'home', path: '/home' }"
        ></sidebar-item>
        <sidebar-item
          :link="{ name: 'Social', icon: 'group', path: '/social' }"
        ></sidebar-item>
        <sidebar-item
          :link="{ name: 'Cultural', icon: 'school', path: '/cultural' }"
        ></sidebar-item>
        <sidebar-item
          :link="{ name: 'Artículos', icon: 'library_books', path: '/articulos' }"
        ></sidebar-item>
        <sidebar-item
          :link="{ name: 'Autores', icon: 'portrait', path: '/autores' }"
        ></sidebar-item>
        <sidebar-item
          :link="{ name: 'Banners', icon: 'image', path: '/banners' }"
        ></sidebar-item>
        <sidebar-item
          :link="{ name: 'Carteles', icon: 'wallpaper', path: '/carteles' }"
        ></sidebar-item>
        <sidebar-item
          :link="{ name: 'Cerrar sesión', icon: 'power_settings_new', path: '/cerrar' }"
        ></sidebar-item>
      </template>
    </side-bar>
    <div class="main-panel">
      <div
        :class="{ content: !$route.meta.hideContent }"
        @click="toggleSidebar"
      >
        <zoom-center-transition :duration="200" mode="out-in">
          <!-- your content here -->
          <router-view :key="$route.fullPath"></router-view>
        </zoom-center-transition>
      </div>
    </div>
  </div>
</template>
<script>
/* eslint-disable no-new */
import PerfectScrollbar from "perfect-scrollbar";
import "perfect-scrollbar/css/perfect-scrollbar.css";

function hasElement(className) {
  return document.getElementsByClassName(className).length > 0;
}

function initScrollbar(className) {
  if (hasElement(className)) {
    new PerfectScrollbar(`.${className}`);
  } else {
    // try to init it later in case this component is loaded async
    setTimeout(() => {
      initScrollbar(className);
    }, 100);
  }
}

import {urlBase,urlImagen} from "../../../urlBase";
import MobileMenu from "./Extra/MobileMenu.vue";
import UserMenu from "./Extra/UserMenu.vue";
import { ZoomCenterTransition } from "vue2-transitions";
import {mapState} from "vuex";

export default {
  components: {
    MobileMenu,
    UserMenu,
    ZoomCenterTransition
  },
  data() {
    return {
      sidebarBackgroundColor: "black",
      sidebarBackground: "green",
      sidebarBackgroundImage: "/img/sidebar-2.jpg",
      sidebarMini: true,
      sidebarImg: true,
      url:urlBase,
      urlImg:urlImagen,
    };
  },
  methods: {
    toggleSidebar() {
      if (this.$sidebar.showSidebar) {
        this.$sidebar.displaySidebar(false);
      }
    },
    minimizeSidebar() {
      if (this.$sidebar) {
        this.$sidebar.toggleMinimize();
      }
    }
  },
  mounted() {
    let docClasses = document.body.classList;
    let isWindows = navigator.platform.startsWith("Win");
    if (isWindows) {
      // if we are on windows OS we activate the perfectScrollbar function
      initScrollbar("sidebar");
      initScrollbar("sidebar-wrapper");
      initScrollbar("main-panel");

      docClasses.add("perfect-scrollbar-on");
    } else {
      docClasses.add("perfect-scrollbar-off");
    }
  },
  watch: {
    sidebarMini() {
      this.minimizeSidebar();
    }
  },
  computed:{
    ...mapState(['usuario'])
  }
};
</script>
<style lang="scss">
$scaleSize: 0.95;
@keyframes zoomIn95 {
  from {
    opacity: 0;
    transform: scale3d($scaleSize, $scaleSize, $scaleSize);
  }
  to {
    opacity: 1;
  }
}
.main-panel .zoomIn {
  animation-name: zoomIn95;
}
@keyframes zoomOut95 {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: scale3d($scaleSize, $scaleSize, $scaleSize);
  }
}
.main-panel .zoomOut {
  animation-name: zoomOut95;
}
</style>
