<template>
  <div class="md-layout">
    <div class="md-layout-item md-size-100">
      <md-card>
        <md-card-header class="md-card-header-icon md-card-header-green">
          <div class="card-icon">
            <md-icon>library_books</md-icon>
          </div>
          <h4 class="title">{{tituloView}}</h4>
        </md-card-header>
        <md-card-content>
          <div class="md-layout">
            <div class="md-layout-item md-size-100 text-center mb-10">
              <div class="file-input">
                <div v-if="!image" class="image-container" >
                  <img :src="avatar" title="" />
                </div>
                <div v-else class="image-container">
                  <img :src="image" />
                </div>
                <div class="button-container">
                  <md-button class="md-danger md-round" @click="removeImage()" v-if="image">
                    <i class="fa fa-times"></i>Remover
                  </md-button>
                  <md-button class="md-success md-round md-fileinput">
                    <template v-if="!image">Seleccionar imagen</template>
                    <template v-else>Cambiar</template>
                    <input type="file" name="circle" @change="onFileChange" ref="fotoFile" v-validate="'required|mimes:image/*'" data-vv-as="Imagen" data-vv-name="file"/>
                  </md-button>
                </div>
                <span v-show="errorsback.file" style="color:red">{{(errorsback.file)?errorsback.file[0]:''}}</span>
                <span v-show="errors.has('file')" style="color:red">{{ errors.first('file')}}</span>
              </div>
            </div>
            <div class="md-layout-item md-size-100 mb-10">
              <label>Galería</label>
              <vue-dropzone ref="galeria" @vdropzone-removed-file="removeFileMulti" @vdropzone-success="responseMulti" id="galeria" :options="galeriaOptions" :useCustomSlot=true>
                <div class="dropzone-custom-content">
                  <h3 class="dropzone-custom-title">Arrastre y suelte los archivos aquí para subirlos</h3>
                  <div class="subtitle">...o haga click para seleccionar un archivo de su computadora</div>
                </div>
              </vue-dropzone>
            </div>
            <div class="md-layout-item md-size-100 mb-10">
              <md-field :class="[{ 'md-valid': !errors.has('video') && touched.video},{ 'md-error': errors.has('video')||errorsback.video }]">
                <label>Video<i style="color:red">*</i><slide-y-down-transition><span v-show="errorsback.video">: {{(errorsback.video)?errorsback.video[0]:''}}</span></slide-y-down-transition></label>
                <md-input v-model="video" type="text" v-validate="'max:100'" data-vv-name="video" data-vv-as="Video"></md-input>
                <span v-show="!errors.has('video')" class="md-helper-text">https://www.youtube.com/watch?v=rInVbN7HYX</span>
                <span v-show="errors.has('video')" class="md-helper-text">{{ errors.first('video')}}</span>
                <slide-y-down-transition> <md-icon class="error" v-show="errors.has('video')||errorsback.video">close</md-icon> </slide-y-down-transition>
                <slide-y-down-transition> <md-icon class="success" v-show="!errors.has('video') && touched.video && !errorsback.video">done</md-icon> </slide-y-down-transition>
              </md-field>
            </div> 
            <div class="md-layout-item md-size-100 mb-10">
              <md-field :class="[{ 'md-valid': !errors.has('titulo') && touched.titulo},{ 'md-error': errors.has('titulo')||errorsback.titulo }]">
                <label>Título<slide-y-down-transition><span v-show="errorsback.titulo">: {{(errorsback.titulo)?errorsback.titulo[0]:''}}</span></slide-y-down-transition></label>
                <md-input v-model="titulo" type="text" v-validate="'required|max:100'" data-vv-name="titulo" data-vv-as="Título"></md-input>
                <span v-show="errors.has('titulo')" class="md-helper-text">{{ errors.first('titulo')}}</span>
                <slide-y-down-transition> <md-icon class="error" v-show="errors.has('titulo')||errorsback.titulo">close</md-icon> </slide-y-down-transition>
                <slide-y-down-transition> <md-icon class="success" v-show="!errors.has('titulo') && touched.titulo && !errorsback.titulo">done</md-icon> </slide-y-down-transition>
              </md-field>
            </div>
            <div class="md-layout-item md-size-100 mb-10" :class="[{ 'green': !errors.has('resumen') && touched.resumen},{ 'red': errors.has('resumen')||errorsback.resumen }]">
              <label for="resumen">Resumen<i style="color:red">*</i><slide-y-down-transition><span v-show="errorsback.resumen">: {{(errorsback.resumen)?errorsback.resumen[0]:''}}</span></slide-y-down-transition></label>
              <vue-editor id="resumen" v-model="resumen" v-validate="'required'" data-vv-name="resumen" data-vv-as="Resumen" class="black"></vue-editor>
              <span v-show="errors.has('resumen')" class="md-helper-text">{{ errors.first('resumen')}}</span>
            </div>
            <div class="md-layout-item md-size-100" :class="[{ 'green': !errors.has('contenido') && touched.contenido},{ 'red': errors.has('contenido')||errorsback.contenido }]">
              <label for="contenido">Contenido<i style="color:red">*</i><slide-y-down-transition><span v-show="errorsback.contenido">: {{(errorsback.contenido)?errorsback.contenido[0]:''}}</span></slide-y-down-transition></label>
              <vue-editor id="contenido" v-model="contenido" v-validate="'required'" data-vv-name="contenido" data-vv-as="Contenido" class="black"></vue-editor>
              <span v-show="errors.has('contenido')" class="md-helper-text">{{ errors.first('contenido')}}</span>
            </div>
          </div>
        </md-card-content>
        <md-card-actions class="md-alignment-left">
          <md-button class="md-success" @click="validateBeforeSubmit()">Enviar</md-button>
        </md-card-actions>
      </md-card>
    </div>
  </div>
</template>
<script>
import {urlBase,urlImagen} from "../../../urlBase";
import Swal from "sweetalert2";
import { VueEditor } from "vue2-editor";
import 'vue2-dropzone/dist/vue2Dropzone.min.css'
import vue2Dropzone from 'vue2-dropzone'
import { SlideYDownTransition } from "vue2-transitions";
export default {
  props: {
    avatar: {
      type: String,
      default: require('@/assets/img/silueta-nota.png')
    },
    id:{
      default:''
    }
  },
  components: {
    VueEditor,
    vueDropzone: vue2Dropzone,
    SlideYDownTransition
  },
  data() {
    return {
      titulo: "",
      resumen: "",
      contenido: "",
      image: "",
      url: urlBase,
      urlImg: urlImagen,
      tituloView:(this.id=='')?'Agregar Noticia':'Editar Noticia',
      imagenes:[],
      imagenesdelete:[],
      imagenesedit:[],
      video:'',
      galeriaOptions: {
        url: `${urlBase}/uploadGaleriaNoticia`,
        acceptedFiles: '.jpg,.png,.jpeg',
        createImageThumbnails: true,
        addRemoveLinks: true,
      },
      errorsback:[],
      touched: {
        video: false,
        titulo: false,
        resumen: false,
        contenido: false
      }
    };
  },
  created(){
    if(this.id!=''){
      this.getNoticia(this.id);
    }
  },
  watch: {
    video() { this.touched.video = true; },
    titulo() { this.touched.titulo = true; },
    resumen() { this.touched.resumen = true; },
    contenido() { this.touched.contenido = true; },
  },
  methods: {
    validateBeforeSubmit() {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.addNoticia()
        } 
        else {
          Swal.fire({
              title: '¡Aún no es posible guardar!',
              text: 'Verifique los campos marcados en rojo',
              type: 'warning',
              confirmButtonText: 'Entendido'
          });
        }
      });
    },
    responseMulti(file, response) {
      this.imagenes.push(response)
    },
    async removeFileMulti(file,error,xhr) {
      if(file.manuallyAdded&&file.manuallyAdded==true){
         var found = this.imagenesedit.find(function(element) {
          return element.nombre==file.name;
        });
        this.imagenesdelete.push(found.url)
      }
      else{
        let url = file.xhr.response
        let index = this.imagenes.indexOf(url)
        this.imagenes.splice(index, 1)
        this.imagenesdelete.push(url)
      }
    },
    removeImage: function() {
      this.image = ""
    },
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.createImage(files[0]);
    },
    createImage(file) {
      if(file.type=="image/png"||file.type=="image/jpg"||file.type=="image/jpeg"){
        var reader = new FileReader();
        var vm = this;
        reader.onload = e => {
          vm.image = e.target.result;
        };
        reader.readAsDataURL(file);
      }
    },
    async addNoticia(){
      const config = { headers: { "Content-Type": "multipart/form-data" } };
      let formData = new FormData()
      formData.append("titulo", this.titulo);
      formData.append("resumen", this.resumen)
      formData.append("contenido",this.contenido)
      formData.append("seccion",this.$route.name)
      formData.append("file", this.$refs.fotoFile.files[0])
      formData.append("imagenes", this.imagenes)
      formData.append("imagenesdelete",this.imagenesdelete)
      formData.append("video", this.video)
      formData.append("id", this.id)
      try {
        let resp = await axios.post(`${this.url}/noticia`,formData,config)
        if(resp){
          Swal.fire({
            title: '¡Guardado correctamente!',
            text: 'Ésta Noticia fue guardado exitosamente.',
            type: 'success',
            confirmButtonText: 'Entendido'
          })
          .then((result) => {
              if (result.value) {
                  let redirect = (this.$route.name=='EditSocial'||this.$route.name=='AddSocial')?'Social':'Cultural'
                  this.$router.push({ name: redirect})
              }
          })
        }
      } catch (error) {
        console.log("error "+error);
      }
    },
    async getNoticia(id) {
      try {
        var { data } = await axios.get(`${this.url}/noticia/${id}`);
        this.titulo = data.noticia.titulo;
        this.resumen = data.noticia.resumen;
        this.contenido = data.noticia.contenido;
        var partes = data.noticia.imagen.split(".")
        var imagen = partes[0]+"_300."+partes[1]
        this.image = this.urlImg+imagen
        this.video = (data.multimedia.video!=null)?data.multimedia.video.url:''
        if(data.multimedia.fotos.length>0){
          data.multimedia.fotos.forEach( (valor, indice, array) => {
            let nombre = `imagen${indice+1}`
            var partes = valor.url.split(".")
            var imagen = partes[0]+"_900."+partes[1]
            var url = this.urlImg+imagen;
            var file = { size: valor.size, name: nombre, type: valor.mime };
            this.$refs.galeria.manuallyAddFile(file, url);
            this.imagenesedit.push({nombre:nombre, url: valor.url})
          });
        }
      } catch (error) {
        console.log(error);
      }
    },
  }
};
</script>
<style lang="scss" scoped>
.md-card .md-card-actions {
  border: none;
}

.md-inline-checkboxes {
  display: inline-flex;

  .md-checkbox {
    margin-top: 15px !important;
  }
}

.md-checkbox,
.md-radio {
  margin-top: 15px;
  margin-bottom: 0.5rem;
}

.md-checkbox,
.md-radio {
  display: flex;
}

.md-radio /deep/ .md-radio-container {
  margin-left: 5px;
  position: relative;
  left: -3px;
}

.md-form-label + .md-layout-item .md-checkbox:not(:first-child),
.md-form-label + .md-layout-item + .md-layout-item .md-radio:not(:first-child),
.md-form-label + .md-layout-item .md-radio {
  margin-top: 0;
}

.form-control-static {
  margin-top: 6px;
}

.descripcion{
  height: 100px !important;
}
.red{
  color:#FF0000;
}
.green{
  color:#4CAF50;
}
.black{
  color:#000000;
}
</style>
